.report-issue-section {
    /* display: flex; */
    align-items: center;
    min-height: 100vh; /* Make sure it takes up the full screen height */
}

.form {
    max-width: 900px; /* Set a maximum width for the form */
    margin: auto;
    padding: 40px;
    display: flex;
    flex-direction: column;
    width: 100%; /* Ensure the form takes full width of its container */
}

.form h2 {
    text-align: left; /* Center the form header */
    font-size: 1.7rem; /* Increase size of the heading */
    margin-bottom: 30px;
}

label {
    font-size: 1rem; /* Increase font size of labels */
    font-weight: 600;
    color: #333; /* Darker text for readability */
}

.form input,
textarea {
    font-family: "Nunito", sans-serif;
    background-color: #f8f7f7;
    border: 1px solid #a9a9a9;
    margin: 0.525rem 0 1.5rem 0;
    padding: 15px; /* Increase padding for larger input fields */
    font-size: 1rem; /* Increase font size */
    color: #000;
    width: 100%; /* Ensure fields take up full width */
    box-sizing: border-box; /* Include padding and border in width calculations */
}

input::placeholder,
textarea::placeholder {
    opacity: 0.7;
    font-size: 0.9rem;
}

textarea {
    resize: vertical; /* Allow resizing of the textarea */
}

.form button {
    max-width: 200px; /* Set button width */
    letter-spacing: 1.9px;
    font-weight: 600;
    text-transform: uppercase;
    padding: 1rem 2rem; /* Increase padding */
    background-color: #000;
    color: white;
    border: none;
    cursor: pointer;
    margin-top: 20px; /* Add margin for spacing */
    align-self:self-start; /* Center the button */
}

button:hover {
    background-color: #1f1e1e;
}
